import { Version } from "../Version";
import { Logger } from "../utils/Logger";
import { TalkService } from "../talk/TalkService";
import { NucleusTag } from "./NucleusTag";
import { NucleusConfig } from "./NucleusConfig";
import { Thread } from "../utils/Thread";

/**
 * 内核对象。所有 API 对象的根入口。
 */
export class Nucleus {
    /**
     * @param {NucleusConfig} [config] 指定配置信息。
     */
    constructor(config) {
        /**
         * 配置信息。
         * @type {NucleusConfig}
         */
        this.config = (config === undefined) ? new NucleusConfig() : config;

        /**
         * 版本信息。
         * @type {Version}
         */
        this.version = new Version();

        /**
         * 内核标签。 
         * @type {NucleusTag}
         */
        this.tag = new NucleusTag();

        // 会话服务。
        this.talkService = new TalkService(this.tag, config);
        /**
         * 会话服务接口实例。
         * @type {TalkService}
         */
        this.talk = this.talkService;

        // 异步启动
        var thr = new Thread(function() {
            this.talk.startup();
        }, this);
        thr.start();

        Logger.i("Nucleus", "Cell " + this.version.getNumbers() + " (Build JavaScript/Web - " + this.version.NAME + ")");
    }

    /**
     * 销毁 Nucleus 的所有内部资源。
     */
    destroy() {
        if (null != this.talkService) {
            this.talkService.shutdown();
            this.talkService = null;
            this.talk = null;
        }

        this.config = null;
        this.version = null;
        this.tag = null;
    }

    /**
     * 获得内核标签。
     *
     * @returns {NucleusTag} 返回内核标签对象实例。
     */
    getTag() {
        return this.tag;
    }

    /**
     * 获得会话服务。
     *
     * @returns {TalkService} 返回内核会话服务。
     */
    getTalkService() {
        return this.talkService;
    }

    /**
     * 激活 WS 插件。
     * 
     * @private
     * @param {*} path 
     * @param {*} callback 
     * @deprecated
     */
    activateWSPlugin(path, callback) {
        var swfjs = document.createElement("script");

        swfjs.onload = function () {
            var wsjs = document.createElement("script");
            wsjs.onload = function () {
                if (callback) {
                    setTimeout(callback, 30);
                }
            };
            wsjs.setAttribute("src", path + "/websocket.js");
            document.body.appendChild(wsjs);
        };

        swfjs.setAttribute("src", path + "/swfobject.js");
        document.body.appendChild(swfjs);

        WEB_SOCKET_SWF_LOCATION = path + "/WebSocketMain.swf";
    }
}
